/***** Example custom styling upload image *****/
.upload-btn-wrapper input[type='file'] {
  position: absolute;
  top: -1000px; }

.upload-btn-wrapper {
  cursor: pointer;
  border: 1px dashed #d9d9d9;
  border-radius: 15px;
  padding: 0px 0px;
  margin: 2px;
  display: inline-block;
  width: 50%; }
  .upload-btn-wrapper:hover {
    border-color: #40a9ff;
    box-shadow: 0 0 0 2px rgba(24, 144, 255, 0.2); }
  .upload-btn-wrapper:active {
    background: #ccf; }
  .upload-btn-wrapper :invalid + span {
    color: #a44; }
  .upload-btn-wrapper :valid + span {
    color: #4a4; }

.ant-menu .avatar {
  display: flex;
  align-items: center; }
  .ant-menu .avatar:hover {
    border-color: #ffffff; }
  .ant-menu .avatar > img {
    border-radius: 50%;
    width: 3rem;
    height: 3rem; }

.auth-button {
  font-size: 14px;
  width: 100px;
  display: block;
  text-align: center; }

.ant-input-wrapper .ant-input-group-addon .ant-btn {
  background-color: #33abe5; }

.menuBar {
  overflow: auto;
  box-shadow: 0 0 30px #f3f1f1; }

.ant-menu-horizontal > .ant-menu-item > a::before {
  bottom: 0px; }

.ant-menu-horizontal > .ant-menu-item,
.ant-menu-horizontal > .ant-menu-submenu {
  top: 0; }

.logo {
  width: 150px;
  float: left; }

.logo a {
  display: inline-block;
  font-size: 20px;
  padding: 19px 20px;
  text-transform: capitalize;
  display: flex;
  justify-content: flex-end;
  margin-right: 2rem;
  color: #33abe5; }

.search-input {
  float: left;
  padding: 20px 20px 15px;
  margin-left: 5rem;
  min-width: 40rem; }

.menuCon {
  width: calc(100% - 150px);
  float: left; }

.menuCon .ant-menu-item {
  padding: 0px 5px; }

.menuCon .ant-menu-submenu-title {
  padding: 10px 20px; }

.menuCon .ant-menu-item a,
.menuCon .ant-menu-submenu-title a {
  padding: 11.5px 15px;
  font-weight: 500; }

.menuCon .ant-menu-horizontal {
  border-bottom: none; }

.menuCon .leftMenu {
  float: left;
  display: flex;
  align-items: center;
  margin-left: 4rem; }
  .menuCon .leftMenu .ant-input-group-wrapper {
    max-width: 500px; }

.menuCon .rightMenu {
  float: right; }

.barsMenu {
  float: right;
  height: 32px;
  padding: 6px;
  margin-top: 8px;
  display: none;
  background: none; }

.barsBtn {
  display: block;
  width: 20px;
  height: 2px;
  background: #1890ff;
  position: relative; }

.barsBtn:after,
.barsBtn:before {
  content: attr(x);
  width: 20px;
  position: absolute;
  top: -6px;
  left: 0;
  height: 2px;
  background: #1890ff; }

.barsBtn:after {
  top: auto;
  bottom: -6px; }

.ant-drawer-body {
  padding: 0; }

.ant-drawer-body .ant-menu-horizontal > .ant-menu-item,
.ant-drawer-body .ant-menu-horizontal > .ant-menu-submenu {
  display: inline-block;
  width: 100%; }

.ant-drawer-body .ant-menu-horizontal {
  border-bottom: none; }

.ant-drawer-body .ant-menu-horizontal > .ant-menu-item:hover {
  border-bottom-color: transparent; }

@media (max-width: 48em) {
  .menuBar {
    display: flex; }
  .menuCon {
    justify-content: flex-end;
    flex: 1 1; }
    .menuCon .leftMenu,
    .menuCon .rightMenu {
      display: none; }
    .menuCon .ant-menu-item,
    .menuCon .ant-menu-submenu-title {
      padding: 1px 20px; }
    .menuCon .barsMenu {
      display: inline-block;
      margin: 14px 0; }
  .logo {
    width: 70px;
    margin: 4px 0; }
    .logo a {
      margin-left: -20px;
      padding: 10px 0px 10px 20px; }
  .ant-drawer-header {
    text-align: center; }
  .ant-drawer-body {
    text-align: center; }
    .ant-drawer-body .ant-input-group-wrapper {
      padding: 15px;
      max-width: 90%; } }

